.panel {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  @media (max-width: 576px) {
    flex-direction: column;
    align-items: center;
  }

  &__logo {
    margin-top: 23px;
    margin-bottom: 23px;
  }

  &__block {
    display: flex;
    list-style-type: none;
  }

  &__ico {
    margin-top: 21px;
    margin-bottom: 6px;
    margin-left: 21px;
        @media (max-width: 576px) {
      margin-top: 0;
      margin-right: 10px;
      margin-left: 10px;
    }
    &--display{
      display: none;
      @media (max-width: 992px) {
        display: initial;
      }

    }
  }

  &__cart {
    position: relative;
    cursor: pointer;
  }

  &__quantity{
    content: "";
    width: 20px;
    height: 20px;
    position: absolute;
    bottom: 23px;
    right: -6px;
    text-align: center;
    font-family: var(--Spartan);
    font-weight: bold;
    font-size: 9px;
    line-height: 18px;
    color: #fff;
    background: #121212;
    border: 2px solid #FFFFFF;
    border-radius: 50%;
    z-index: 4;
    @media (max-width: 576px) {
      bottom: 10px;
    }
  }

  &__menu-burger {
    display: none;
    @media (max-width: 992px) {
      position: relative;
      display: flex;
      flex-direction: column;
      cursor: pointer;
      border: none;
      background-color: #FFFFFF;
    }

  }

  &__burger {
    @media (max-width: 992px) {
      margin: 3px 0;
      width: 20px;
      height: 2px;
      background-color: #121212;
      z-index: 44;
    }

    &--1 {
      background-color: white;
    }
    &--2 {

      transform: rotate(45deg);
    }
    &--3 {
      position: absolute;
      top: 50%;
      transform: rotate(-45deg);
    }

  }
}